.searchBox {
    border-radius: 5px;
    height: 5%;
    width: 50%;
    text-align: center;
    margin-right: 1%;
    font-size: 45px;
    font-family: 'Playfair Display SC', serif;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    ;
}

.searchRecipe {
    background-color: #4f7a7f;
    border: none;
    border-radius: 10px;
    color: white;
    padding: 8px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 200%;
    height: 100%;
    width: 20%;
    margin-top: -2%;
    font-family: 'chalk', Fallback, serif;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-shadow: 2px 2px 4px #000000;
    ;
}

.dishPic {
    min-width: 25vw;
    float: left;
    max-height: 50vh;
    border-radius: 10px;
}

.dishName {
    /* margin-left: 50%; */
    font-size: 45px;
    font-family: 'chalk', Fallback, serif;
    color: white;
    text-shadow: 2px 2px 4px #000000;
}

.recipeList {
    display: block;
    /* text-justify: center; */
    padding: 20px;
    text-decoration: none;
    border: 1px solid #777777;
    margin-top: 2%;
    margin-left: 2%;
    margin-right: 2%;
    border-radius: 10px;
    font-size: 35px;
    overflow: auto;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: rgba(119, 119, 119, .6);
}

.searchBox {
    font-family: 'chalk', Fallback, serif;
}

#recipeResults {
    text-align: center;
    padding: 0 15%;
}

#recipeUL {
    list-style-type: none;
    margin: 10px;
    padding: 0;
    display: grid;
    grid-template-columns: 50% 50%;
    overflow: auto;
}

#searchForm {
    text-align: center;
    font-family: 'chalk', Fallback, serif;

}

#searchRecipes {
    margin-left: 12%;
    width: 75vw;
}

#imgContainer {
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    position: relative;
}

img {
    margin: auto;
    display: inline-block;
}

#nameContainer {
    display: block;
    text-align: center;
}

#imgContainer #saveBtn {
    z-index: 999;
    position: absolute;
    top: 0;
    right: 10%;
}

@media (max-width: 1920px) {

    .dishPic {
        /* max-width: 55vw; */
        float: left;
        padding-top: 0;
        padding-bottom: 0;
        margin-top: 0;
        margin-bottom: 0;
    }
}

@media (max-width: 950px) {
    #recipeUL {
        grid-template-columns: 100%;
    }
    .dishPic {
        max-width: 60vw;
        float: left;
        max-height: 50vh;
        padding-top: 0;
        padding-bottom: 0;
        margin-top: 0;
        margin-bottom: 0;
    }
}

@media (max-width: 615px) {
    .searchRecipe {
        font-size: 25px;
    }
    #recipeUL {
        grid-template-columns: 100%;
    }
    .dishPic {
        max-width: 50vw;
        float: left;
        max-height: 35vh;
        padding-top: 0;
        padding-bottom: 0;
        margin-top: 0;
        margin-bottom: 0;
    }
}

@media (max-width: 480px) {
    #recipeUL {
        grid-template-columns: 100%;
    }
    .dishPic {
        max-width: 40vw;
        float: left;
        max-height: 25vh;
        padding-top: 0;
        padding-bottom: 0;
        margin-top: 0;
        margin-bottom: 0;
    }
    .dishName {
        font-size: 30px;
    }
    .searchRecipe {
        font-size: 12px;
    }
}


